.card {
  width: 200px;
  padding: 30px 14px 15px;
  margin: 0 24px 50px;

  background: $color-white;
  border: 1px solid $color-white-2;
  box-sizing: border-box;
  border-radius: 5px;
}

.card:hover {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
}

.card__img {
  width: 160px;
  height: 160px;
  margin: 0 auto 19px;
  align-self: center;
}

.card__picture {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.picture--1 {
  height: 134px;
}

.card__name {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  margin-bottom: 5px;
}

.card-font {
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  color: $color-black;
}

.card__code {
  margin-bottom: 15px;
}

.card__about {
  @include flex-sb-c;
  margin-bottom: 20px;
}

.card__rating {
  display: flex;
  align-items: baseline;
  height: 15px;
}

.card__rating-star {
  height: 15px;
  width: 15px;
  background-image: url("../images/star-active.svg");
  background-repeat: no-repeat;
  background-position: 10% 50%;
  background-size: 13px;
  background-origin: padding-box;
  padding-right: 4px;
  box-sizing: content-box;
}

.card__rating-star:last-child {
  background-image: url("../images/star.svg");
}

.card__priсe {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 20px;
}

.card__price-value {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}

.card__btn {
  height: 40px;
  width: 100%;
  display: inline-block;
  padding: 12px 58px;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  color: $color-white;
  background: $color-blue;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.card__btn:hover {
  color: $color-blue;
  background: $color-white;
  border: 1px solid $color-blue;
}
